.Nav {
    display: flex;
    justify-content: start;
    align-items: center;
    font-size: 32px;
    /* border-bottom: 1px solid #3e3e3e; */
}

.Nav-icon img {
    height: 40px;
    width: 40px;
    margin-right: 4px;
    padding-bottom: 8px;
    filter: invert(100%);
}

.Nav-title {
    font-family: "Russo One", sans-serif;
    margin: 0 8px 0 0;
    padding-bottom: 4px;
}

.Nav-searchbar {
    width: 100%;
    max-width: 400px;
    max-height: 42px;
    min-height: 30px;
    display: flex;
    align-items: center;
    border: 1px solid white;
    border-radius: 8px;
    margin: 0 auto 6px auto;
}

.Nav-searchbar input {
    width: 100%;
    height: 42px;
    display: flex;
    align-items: center;
    border: none;
    outline: none;
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    padding: 4px 8px 4px 0;
    background-color: transparent;
}

.Nav-searchbar input:focus {
    outline: none;
    color: white;
}

.Nav-iconSearch {
    border: none;
    background-color: transparent;
    cursor: pointer;
    margin-right: 8px;
    padding: 8px 0 6px 8px;
}

.Nav-iconSearch:hover {
    color: white;
}

.Nav-buttons {
    display: flex;
    align-items: center;
    margin-left: 8px;
    margin-bottom: 6px;
}

.Nav button {
    font-size: 15px;
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    background-color: #3e3e3e;
    cursor: pointer;
    text-wrap: nowrap;
}

.Nav button:hover {
    background-color: #545454;
}

@media (max-width: 624px) {
    .Nav-title {
        display: none;
    }
}

@media (max-width: 420px) {
    .Nav button {
        text-wrap: wrap;
        padding: 4px;
    }
}
